body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.shoppingCart {
  display: flex;
  flex-direction: row;
  gap: 50px;
  padding: 10px;
  margin: 10px 30px 10px 30px;

}

.body-design {
  width: 100%;
  height: 300px;
  background-color: rgba(0, 0, 0, 0.801);
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  padding: 100px 0;
}


.Nav-bar {
  background-color: rgb(255, 255, 255);
  padding: 10px;
}

.footer-div {
  background-color: rgba(0, 0, 0, 0.801);
  color: white;
  width: 100%;
  height: 100px;
  text-align: center;
  padding: 50px 0;
}

.img-div {
  width: 100%;
  height: 100%;
  text-align: center;
}

.h1-home {
  text-align: center;
}

.first-row-price {
  display: flex;
  flex-direction: row;
  gap: 20px;
  margin: 10px;
  padding: 10px;
}

.second-row-price {
  display: flex;
  flex-direction: row;
  gap: 20px;
  margin: 10px;
  padding: 10px;
}


.image-width>img {
  width: 200px;
  height: 200px;
  padding: 20px;
  margin: 10px;
  justify-content: center;
}

.price-cart {
  display: flex;
  padding: 10px;
  margin: 20px;
  flex-direction: row;
  gap: 30px;
  flex-wrap: wrap;
  justify-content: center;
}

.complete-body {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.body-design-cart {
  position: absolute;
  float: right;
  margin: 10px;
  width: 200px;
}

.body-design-cart:hover {
  content: "Clear";
}

.body-design-total {
  /* position: absolute; */
  display: flex;
  flex-direction: row;
  gap: 10px;
  margin: 10px;
  width: 350px;
  top: 60px;
  height: 50px;
}

.danger-button {
  position: relative;
  margin: 5px;
  padding: 5px;
  width: 40%;
  height: 50px;
  left: 220px;
  top: 5px;
}

.cart-img {
  width: 40px;
  height: 25px;
  color: black;
  margin-right: 20px;
  margin-bottom: 5px;
  background-attachment: fixed;
}

.header4 {
  padding: 10px;
  margin: 10px;
}

.contact-card {
  line-height: 40px;
}

.cardTitle {
  padding: 10px;
}

.hrefLinks {
  border: 1px solid grey;
  padding: 10px;
  text-decoration: none;
  color: white;
}

.hrefLinks:hover {
  color: rgba(52, 192, 247, 0.733);
  background-color: white;
  font-weight: bold;
}

.span-tag {
  border: 1px solid grey;
  padding: 5px;
  margin: 5px;
  color: white;
  background-color: black;
  position: absolute;
  top: 0;
  right: 0;
}
